}
.mt-5 {
  margin-top: 15px!important;
}
.p-1 {
  padding: 3px;
}
.p-2 {
  padding: 6px;
}
.pl-5 {
  padding-left: 15px;
}
.pointer {
  cursor: pointer;
}
.block {
  display: block;
}
.flex {
  display: flex;
}
.noShadow {
  box-shadow: none!important;
}
.center {
  display: flex;
  align-items: center;
  justify-content: center;
}
.relative {
  position: relative;
}
.color-blue {
  color: #1976d2;
  color: var(--blue);
}
.color-red {
  color: #f01616;
  color: var(--red);
}
.menuitemselect {
  transition: 0.1s;
}
.menuitemselect:hover {
  background-color: rgb(237, 237, 237)!important;
}
